 /*  comparisonSection */
  .workWithPagesSection {
    position: relative;
    padding-bottom: 100%; /* to maintain aspect ratio (responsive!)   comparisonSection*/
    overflow: hidden;
  
  }

   /*   comparisonImage*/
  .singlePageSection {
    width: 100%;
    height: 100%;
     
  }
   /*  afterImage */
  .translatingSection {
    position: absolute;
    overflow: hidden;
    top: 0;
    transform: translate(100%, 0px);
  }
   /*  afterImage img */
  .translatingSection > div {
    transform: translate(-100%, 0px);
  }
   /*  comparisonImage img */
  .singlePageSection > div {
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    overflow-x: hidden;
     
  }