

  /*

  body {
    height: 300vh;
  
   
    overflow-x: hidden;
  }
  */
  h1, h2 {
    font-weight: 400;
    max-width: none;
  }
  
  .comparisonSection {
    position: relative;
    padding-bottom: 100%; /* to maintain aspect ratio (responsive!) */
    overflow: hidden;
  }
  .comparisonImage {
    width: 100%;
    height: 100%;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  }
  .afterImage {
    position: absolute;
    overflow: hidden;
    top: 0;
    transform: translate(100%, 0px);
  }
  .afterImage img {
    transform: translate(-100%, 0px);
  }
  .afterImageb {
    position: absolute;
    overflow: hidden;
    top: 0;
    transform: translate(100%, 0px);
  }
  .afterImageb img {
    transform: translate(-100%, 0px);
  }
  .comparisonImage img {
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    overflow-x: hidden;
     
  }
  